<script lang="coffeescript">// /lists
// List Index
var addRootItem, getOrSet, menuSelect, pageName, resourceList, viewOptions;

import {
  push
} from "svelte-spa-router";

import RootItemList from '../../helpers/root_item_list.js';

import {
  Row,
  RowCell
} from "../../components/Flex";

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import List from './_List.svelte';

import Menu from "../_Common/Menus/Lists.svelte";

import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte";

// Local Vars
export var list = [];

// List Model
resourceList = new RootItemList;

getOrSet = function(parameter, override) {
  if (localStorage.getItem(parameter) != null) {
    return localStorage.getItem(parameter);
  } else {
    localStorage.setItem(parameter, override);
    return override;
  }
};

viewOptions = {
  include_completed: getOrSet("radar_lists_include_completed", true) === "true",
  favorites_only: getOrSet("radar_lists_favorites_only", false) === "true",
  sort_by: getOrSet("radar_lists_sort_by", "name"),
  sort_descending: getOrSet("radar_lists_sort_descending", false) === "true",
  context: getOrSet("radar_lists_view", "current")
};

// lists_sort_descending
// lists_sort_by
// lists_
// ongoingOnly = getOrSet("radar_ongoing_only", false) == "true"
addRootItem = function(e) {
  var scb;
  scb = function(inserted) {
    return push(`/lists/${inserted.id // redirect to list page
}`);
  };
  return resourceList.insert(e.detail, scb, null, null);
};

menuSelect = function(e) {
  console.error(`menuSelect ${e.detail}`);
  switch (e.detail) {
    case "current":
    case "archived":
      viewOptions.context = e.detail;
      return localStorage.setItem("radar_lists_view", e.detail);
    case "toggle_favorites":
      viewOptions.favorites_only = !viewOptions.favorites_only;
      return localStorage.setItem("radar_lists_favorites_only", viewOptions.favorites_only);
    case "toggle_completed":
      viewOptions.include_completed = !viewOptions.include_completed;
      return localStorage.setItem("radar_lists_include_completed", viewOptions.include_completed);
    case "toggle_sort_by_name":
      localStorage.setItem("radar_lists_sort_by", "name");
      if (viewOptions.sort_by !== "name") {
        viewOptions.sort_by = "name";
        viewOptions.sort_descending = false;
      } else {
        viewOptions.sort_descending = !viewOptions.sort_descending;
      }
      return localStorage.setItem("radar_lists_sort_descending", viewOptions.sort_descending);
    case "toggle_sort_by_date":
      localStorage.setItem("radar_lists_sort_by", "due_date");
      if (viewOptions.sort_by !== "due_date") {
        viewOptions.sort_by = "due_date";
        viewOptions.sort_descending = false;
      } else {
        viewOptions.sort_descending = !viewOptions.sort_descending;
      }
      return localStorage.setItem("radar_lists_sort_descending", viewOptions.sort_descending);
    default:
      return console.log(`do nothing ${e.detail}`);
  }
};

pageName = "Lists";

$: context = viewOptions.context;
</script>

<svelte:head>
  <title>{pageName}</title>
</svelte:head>



<PageContent>
    <PageHeader icon={true}>
      <div slot="icon">
        {#if context == "current"}
          <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 24 24"><path d="M4 10.5c-.83 0-1.5.67-1.5 1.5s.67 1.5 1.5 1.5 1.5-.67 1.5-1.5-.67-1.5-1.5-1.5zm0-6c-.83 0-1.5.67-1.5 1.5S3.17 7.5 4 7.5 5.5 6.83 5.5 6 4.83 4.5 4 4.5zm0 12c-.83 0-1.5.68-1.5 1.5s.68 1.5 1.5 1.5 1.5-.68 1.5-1.5-.67-1.5-1.5-1.5zM7 19h14v-2H7v2zm0-6h14v-2H7v2zm0-8v2h14V5H7z"/></svg>
        {:else}
          <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 24 24"><path d="M3,3H21V7H3V3M4,8H20V21H4V8M9.5,11A0.5,0.5 0 0,0 9,11.5V13H15V11.5A0.5,0.5 0 0,0 14.5,11H9.5Z"/></svg>
        {/if}
      </div>
      <Row style="max-width: 100%;">
        <RowCell flex style="overflow-x: hidden">
          <h1>{context.titleize()} {pageName}</h1>
        </RowCell>
        <RowCell style="padding-top: 16px; width: 32px" flex="initial">
          <Menu on:select={menuSelect} bind:options={viewOptions}/>
        </RowCell>
      </Row>

    </PageHeader>

  <section>

    <List context={context} options={viewOptions} />
    

    <QuickEntry on:add={addRootItem} stubPlaceholder="Add List" />
    
  </section>
</PageContent>

<style lang="sass">svg path {
  fill: var(--light-gray); }

/*# sourceMappingURL=Index.svelte.css.map */</style>