        div[container] [slider-ticks] table:first-child tr:first-child td:last-child {
          border-right: 1px solid #c8c8c8; }
      div[container] [slider-ticks] table:first-child tr td {
        text-align: right; }
        div[container] [slider-ticks] table:first-child tr td:first-child {
          text-align: left; }
  div[container] [slider-tick-labels] {
    position: absolute;
    top: 36px;
    height: 24px; }
    div[container] [slider-tick-labels] [tick-label] {
      position: absolute;
      top: 0px; }
  div[container] [slider] {
    top: 0px;
