<script lang="coffeescript">var component, dispatch, dueDateState, isShowing, newValue, popover, tippyObject;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import tippy from 'tippy.js';

import DueDate from "./_DueDate.svelte";

import RelativeDateFormat from "../../../components/Format/RelativeDateFormat.svelte";

tippyObject = void 0;

component = void 0;

export var value = Date.create();

export var style = void 0;

export var item = void 0;

export var isListItem = true;

export var idAttribute = "id";

isShowing = false;

newValue = void 0;

import {
  Popover
} from "../../../components/Overlay";

import {
  Column,
  ColumnCell,
  Row,
  RowCell
} from "../../../components/Flex";

import Calendar from '../../../components/Date/DatePicker/Calendar.svelte';

popover = function(element, content) {
  var tippyOptions;
  tippyOptions = {
    theme: "datepicker",
    trigger: "click",
    placement: "bottom-end",
    interactive: true,
    popperOptions: {
      modifiers: [
        {
          name: "flip",
          options: {
            fallbackPlacements: ["top",
        "right",
        "left"]
          }
        }
      ]
    }
  };
  if (typeof content === 'function') {
    tippyOptions.onCreate = function(instance) {
      component = new content({
        target: instance.popper.querySelector('.tippy-content')
      });
      component.$set({
        dateValue: newValue,
        isShowing: isShowing
      });
      component.$on("clear", function(e) {
        var eventObject;
        tippyObject.hide();
        console.error("clear");
        newValue = null;
        dispatch("close");
        eventObject = {
          due_date: newValue,
          ancestry: item.ancestry
        };
        eventObject[idAttribute] = item[idAttribute];
        dispatch("update", eventObject);
        return component.$set({
          dateValue: newValue
        });
      });
      component.$on("set-date", function(e) {
        var eventObject;
        console.error("set-date");
        value = e.detail;
        console.log(e.detail);
        newValue = e.detail.format("{yyyy}-{MM}-{dd}");
        component.$set({
          dateValue: newValue
        });
        eventObject = {
          due_date: newValue,
          ancestry: item.ancestry
        };
        eventObject[idAttribute] = item[idAttribute];
        dispatch("update", eventObject);
        return tippyObject.hide();
      });
    };
    tippyOptions.onHide = function(instance) {
      //console.error "TIPPY HIDE"
      isShowing = false;
      return component.$set({
        isShowing: isShowing
      });
    };
    tippyOptions.onShow = function(instance) {
      //console.error "TIPPY SHOW"
      isShowing = true;
      return component.$set({
        isShowing: isShowing
      });
    };
  } else {
    tippyOptions.content = content;
  }
  if (Object.keys(tippyOptions).length === 0) {
    tippyObject = tippy(element);
  } else {
    tippyObject = tippy(element, tippyOptions);
  }
  return;
  return {
    update: function(params) {
      console.error("update");
      if (component) {
        component.$set({
          dateValue: newValue
        });
      }
    },
    destroy: function() {
      tippyObject.destroy();
      console.error("tippy destroy");
      if (component) {
        // debugger
        component.$destroy();
      }
    }
  };
};

dueDateState = function(item, date) {
  if (!Object.has(item, "percent_complete")) {
    item["percent_complete"] = 0;
  }
  if (date != null) {
    if (item.percent_complete === 100) {
      return "complete";
    } else {
      if (Object.isDate(date)) {
        return date.comparedTo(Date.create());
      } else {
        return Date.create(date).comparedTo(Date.create());
      }
    }
  } else {
    return "before";
  }
};

$: newValue = value;

$: state = dueDateState(item,newValue);
</script>

<style lang="sass">[calendar] {
  padding: 0px 4px; }

input {
  width: 100px;
  display: inline-block; }

[duedate][islistitem="true"] {
  margin-left: 8px; }

[duedate][islistitem="false"] {
  margin-right: 8px; }

[duedate][state="before"] {
  color: var(--dark-red); }

[duedate][state="on"] {
  color: var(--green); }

[duedate][state="after"] {
  color: var(--gray); }

[duedate][state="complete"] {
  color: var(--light-gray); }

/*# sourceMappingURL=DueDate.svelte.css.map */</style>

<div use:popover={DueDate}  show-on-hover is-showing={isShowing}>

    {#if newValue == null}
      {#if isListItem}
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M22 3h-3V1h-2v2H7V1H5v2H2v20h20V3zm-2 18H4V8h16v13z" fill="var(--gray)"/></svg>
      {:else}
        <a duedate {state} {isListItem}><em>No date assigned</em></a>
      {/if}
    {:else}
      <a duedate {state} {isListItem}><em>
        {#if !isListItem}
          <span duedate {state}>Due </span>
        {/if}
        <RelativeDateFormat bind:value={newValue}/>
      </em></a>

    {/if}

</div>
