<script lang="coffeescript">
/*
 * doesn''t wipe deleted docs from db
db.allDocs({ include_docs: false })
.then (response)->
console.error "DESTROY ALL DOCS"
docsToBeDeleted = response.rows.map (o)->

return {
  _id: o.id
  _rev: o.value.rev
  _deleted: true
}
console.debug docsToBeDeleted
db.bulkDocs docsToBeDeleted
.then (result)->
console.error "Attempt to delete all docs"
console.log result
console.error "COMPACT"
db.compact()
.then (result)->
  console.error "compacted"
  console.debug result
 */
var destroy, dispatch, edition, forceSync, importData, notificationPermssionRequest, replicationDb, replicationEnabled, replicationRequiresAuthentication, resource, restore, saveAs, toggleSync, updateDb, updatePassword, updateUser;

import PageContent from "../_Common/PageContent.svelte";

import PageHeader from "../_Common/PageHeader.svelte";

import FileSaver from "file-saver";

import md5 from "md5";

import RootItemList from '../../helpers/root_item_list.js';

import Checkbox from '../../components/Form/Checkbox/Checkbox.svelte';

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import InlinePassword from '../../components/Form/InlineInput/InlinePassword.svelte';

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var files = void 0;

export var fileObj = {};

export var jsonFile = void 0;

$: if (files) { fileObj = files };

resource = new RootItemList();

replicationEnabled = localStorage.getItem("radar_sync");

replicationDb = localStorage.getItem("radar_remote_db");

replicationRequiresAuthentication = localStorage.getItem("sync_authentication") || "false";

toggleSync = function() {
  localStorage.setItem("radar_sync", !(replicationEnabled === 'true'));
  return replicationEnabled = localStorage.getItem("radar_sync");
};

importData = function() {
  var fileArray, reader;
  console.error("importData");
  console.debug(files);
  if (files != null) {
    console.log("file info");
    fileArray = Array.from(files);
    console.debug(fileArray.first());
    reader = new FileReader();
    reader.onloadend = function(response) {
      var jsonToData;
      console.log("READ FILE");
      console.log(response);
      jsonFile = response.target.result;
      jsonToData = JSON.parse(jsonFile).map(function(o) {
        delete o._rev;
        return o;
      });
      jsonToData.forEach(function(item) {
        item.id = uuid();
        item["_id"] = item.id;
        return item.name = `${item.name} imported at ${Date.create()}`;
      });
      return db.bulkDocs(jsonToData).then(function(result) {
        console.error("imported");
        return console.debug(result);
      });
    };
    return reader.readAsText(fileArray.first());
  }
};

updateDb = function(e) {
  return console.log(e);
};

updateUser = function(e) {
  return console.log(e);
};

updatePassword = function(e) {
  return console.log(e);
};

saveAs = function() {
  var projectLoadedScb;
  projectLoadedScb = function(projects) {
    var blob, jsonString, list;
    list = projects;
    jsonString = JSON.stringify(list, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    // note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
    // use a different handler for electron
    return FileSaver.saveAs(blob, `braintapper-radar-archive-${Date.create().toTimestampId()}--${md5(jsonString)}.json`);
  };
  return resource.select(projectLoadedScb, null, null);
};

forceSync = function() {
  console.log(remoteDb);
  if (typeof remoteDb !== "undefined" && remoteDb !== null) {
    console.log("syncing to remote db");
    return db.sync(remoteDb, {
      retry: true
    }).on('complete', function() {
      return console.log("complete!");
    }).on('change', function(change) {
      // yo, something changed!
      console.log("sync change");
      return console.log(change);
    }).on('paused', function(info) {
      // replication was paused, usually because of a lost connection
      console.log("sync paused");
      return console.log(info);
    }).on('active', function(info) {
      // replication was resumed
      console.log("sync active");
      return console.log(info);
    }).on('error', function(err) {
      console.log("sync error");
      return console.log(err);
    });
  }
};

restore = function() {};

destroy = function() {
  return db.destroy().then(function(response) {
    console.log("destroyed");
    console.debug(response);
    window.createPouchDb();
    return window.mounted();
  }).catch(function(err) {
    console.error("destroy error");
    return console.log(err);
  });
};

notificationPermssionRequest = function() {
  return Notification.requestPermission().then(function(permission) {
    var notification;
    // If the user accepts, let''s create a notification
    if (permission === 'granted') {
      notification = new Notification('Hi there!');
    } else {
      console.log("not granted");
    }
  });
};

if (electron) {
  edition = "Desktop";
} else {
  edition = "Web";
}

$: disableReplication = (replicationEnabled != 'true');
</script>
<style lang="sass">article {
  margin: 0px; }

button [disabled] {
  background: var(--lightest-gray) !important; }

/*# sourceMappingURL=Settings.svelte.css.map */</style>

<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <a name="top"></a>
  <PageHeader><h1>Settings</h1></PageHeader>
  <article type="content">


    <h2>Interface</h2>

    <div>
    Interface tweaks will be going here.
    </div>


    <h2>Data</h2>

    <h2> Warning, Items Below May Not Be Functional</h2>

    <h3>Export All Data</h3>
    <p>This will save your collections as a JSON file into your downloads folder. This does not export your settings.</p>
    <button type="button" primary on:click={saveAs}>Save to Disk</button>

    <h3>Import</h3>
    <p>Import data from a JSON file. This will create new IDs for all incoming items, so if you are importing a backup, you may end up with items having duplicate names.</p>
    <button type="button" primary on:click={importData}>Import</button>


    <input type="file" id="input" bind:files />
    <div>
    {#if jsonFile}
    	<pre>{jsonFile}</pre>
    {/if}
    </div>
    <button type="button" on:click={importData}>Import</button>


    <h4>Import from URL</h4>
    <p>Coming Soon</p>

    <h3>Danger Zone</h3>

    <h2>Restore</h2>
    <p>This will clear all existing data (including on any synchronized databases) and load the contents of an export file into your system. Your settings will remain intact.</p>
    <button type="button" critical on:click={restore} disabled={true}>Restore</button>

    <h2>Delete All Local Data</h2>
    <p>This will delete your local database but not the remote database. It will disable sync settings and delete the remote address of your remote database.</p>
    <button type="button" critical on:click={destroy}>Delete</button>



  </article>

</PageContent>
