<script lang="coffeescript">var current, currentCalendar, currentCalendarHeader, currentMonth, dispatch, displayDate, eventsForDay, generate, next, nextYear, previous, previousYear, pushDay, pushMode, pushMonth, pushQuarter, pushWeek, pushYear, setDay, setIsShowing, setNextWeekday, today, togglepushMode, tomorrow, weeks, yesterday;

export var value = null;

export var events = [];

export var isShowing = void 0;

import {
  Row,
  RowCell
} from "../../Flex";

import {
  createEventDispatcher,
  onDestroy
} from 'svelte';

dispatch = createEventDispatcher();

weeks = [];

// console.log events
eventsForDay = function(day) {
  if (events.length === 0) {
    return [];
  } else {
    return events.filter(function(event) {
      return Date.create(event.start).format("{MM}{dd}") === Date.create(day).format("{MM}{dd}");
    });
  }
};

currentCalendar = [];

currentMonth = null;

currentCalendarHeader = null;

generate = function(baseDate) {
  var calendarEnd, calendarStart, count, currentWeek, outputWeeks, runningDate;
  //console.error "generate"
  //console.log JSON.stringify(baseDate)
  outputWeeks = [];
  currentCalendarHeader = baseDate.format("{Month} {yyyy}");
  currentMonth = baseDate.format("{M}");
  calendarStart = baseDate.clone().beginningOfMonth().beginningOfWeek();
  calendarEnd = baseDate.clone().endOfMonth().endOfWeek();
  runningDate = calendarStart.clone();
  //console.error "runningDate: #{runningDate}"
  currentWeek = [];
  count = 0;
  while (runningDate.isBetween(calendarStart, calendarEnd)) {
    currentWeek.push(runningDate.clone());
    runningDate.addDays(1);
    count++;
    if (count === 7) {
      outputWeeks.push(currentWeek);
      currentWeek = [];
      count = 0;
    }
  }
  return weeks = outputWeeks;
};

displayDate = void 0;

current = function(dateValue) {
  // console.error "CURRENT"

  // console.log "dateValue"
  // console.log dateValue
  if (value != null) {
    // console.log "value not null"
    // console.log "Value"
    // console.log value
    if (Object.isDate(dateValue)) {
      // console.log "IS ALREADY A DATE"
      return displayDate = dateValue;
    } else {
      // console.log "NOT A DATE, MAKIE IT A DATE"
      return displayDate = Date.create(dateValue);
    }
  } else {
    // console.log "NO VALUE, today"
    return displayDate = Date.create();
  }
};

// console.log "displayDate"
// console.log displayDate
// console.error "-------------------------------------"
next = function() {
  //console.error "next()"
  //console.log displayDate
  //console.log Object.isDate(displayDate)
  displayDate.addMonths(1);
  //console.log "displayDate #{value}"
  //console.log "value #{value}"    
  //console.log value
  return generate(displayDate);
};

previous = function() {
  displayDate.addMonths(-1);
  return generate(displayDate);
};

// console.log "value #{value}"
nextYear = function() {
  displayDate.addYears(1);
  return generate(displayDate);
};

// console.log "value #{value}"
previousYear = function() {
  displayDate.addYears(-1);
  return generate(displayDate);
};

// console.log "value #{value}"
today = function() {
  return setDay(Date.create());
};

// console.log "today"
yesterday = function() {
  return setDay(Date.create("yesterday"));
};

tomorrow = function() {
  return setDay(Date.create("tomorrow"));
};

pushMode = 1; // forward

pushDay = function(interval) {
  var target, targetInterval;
  targetInterval = `${(interval || 1) * pushMode} day`;
  //console.log "target interval #{targetInterval}"
  target = displayDate.advance(targetInterval);
  //console.error "push day #{target}"
  return setDay(target);
};

pushWeek = function() {
  var target;
  target = displayDate.advance(`${7 * pushMode} days`);
  //console.error "push week #{target}"
  return setDay(target);
};

pushMonth = function() {
  var target;
  target = displayDate.advance(`${1 * pushMode} month`);
  //console.error "push month #{target}"
  return setDay(target);
};

pushQuarter = function() {
  var target;
  target = displayDate.advance(`${3 * pushMode} months`);
  //console.error "push quarter #{target}"
  return setDay(target);
};

pushYear = function() {
  var target;
  target = displayDate.advance(`${1 * pushMode} year`);
  //console.error "push year #{target}"
  return setDay(target);
};

setDay = function(day) {
  // console.log "CALENDAR SETDAY #{day}"
  pushMode = 1; // reset
  return dispatch("change", day);
};

togglepushMode = function() {
  return pushMode = pushMode * -1;
};

setNextWeekday = function(weekday) { // jump to the next specified weekday
  var differential, target;
  
  // sun = 0
  // sat = 6

  // if current weekday = weekday, then it''s next week
  //console.error "setNextWeekday"
  //console.log "displayDate getWeekday: #{displayDate.getWeekday()}"
  //console.log "weekday = weekday #{weekday}"

  //console.log "weekday >  displayDate.getWeekday  : #{weekday >  displayDate.getWeekday()}"
  if (weekday > displayDate.getWeekday()) {
    // same week
    differential = weekday - displayDate.getWeekday();
  } else {
    
    // next week
    differential = 7 - displayDate.getWeekday() + weekday;
  }
  target = displayDate.advance(`${differential} days`);
  return setDay(target);
};

onDestroy(function(e) {});

// console.error "DESTROY"
setIsShowing = function(state) {
  if (!state) { // reset
    pushMode = 1;
    return current(value);
  }
};

$: current(value);

$: generate(displayDate);

$: isPopoverActive = setIsShowing(isShowing);
</script>





<div container>
  <table calendar>
    <thead>
      <tr>
        <td colspan="7">
          <Row>
            <RowCell style="width: 33%">
              <button type="button" on:click={yesterday} dayshortcut>
                Yesterday
              </button>
            </RowCell>
            <RowCell flex="flex" style="width: 33%">
              <button type="button" on:click={today} dayshortcut>
                Today
              </button>
              </RowCell>
            <RowCell style="width: 33%">
              <button type="button" on:click={tomorrow} dayshortcut>
                Tomorrow
              </button>

            </RowCell>
          </Row>
        </td>
      </tr>
      <tr>
        <td colspan="7">
          <Row>
            <RowCell>
              <button type="button" icon on:click={previousYear}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M18.41 16.59L13.82 12l4.59-4.59L17 6l-6 6 6 6 1.41-1.41zM6 6h2v12H6V6z"/></svg>
              </button>

              <button type="button" icon on:click={previous}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12l4.58-4.59z" class="fill"/></svg>
              </button>
            </RowCell>
            <RowCell flex="flex"><div monthname>{currentCalendarHeader}</div></RowCell>
            <RowCell>
              <button type="button" icon on:click={next}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z" class="fill"/></svg>
              </button>
              <button type="button" icon on:click={nextYear}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M5.59 7.41L10.18 12l-4.59 4.59L7 18l6-6-6-6-1.41 1.41zM16 6h2v12h-2V6z"/></svg>
              </button>
            </RowCell>
          </Row>
        </td>
      </tr>
      <tr weekdays>
        <td><button on:click={setNextWeekday(0)}>S</button></td>
        <td><button on:click={setNextWeekday(1)}>M</button></td>
        <td><button on:click={setNextWeekday(2)}>T</button></td>
        <td><button on:click={setNextWeekday(3)}>W</button></td>
        <td><button on:click={setNextWeekday(4)}>T</button></td>
        <td><button on:click={setNextWeekday(5)}>F</button></td>
        <td><button on:click={setNextWeekday(6)}>S</button></td>
      </tr>
    </thead>
    <tbody>
      {#each weeks as week, weekIndex}
        <tr>

          {#each week as day, dayindex}
            {#if (parseInt(day.format("{M}")) != parseInt(currentMonth)) }
              {#if (weekIndex < 1) }
                <td month="previous" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {:else}
                <td month="next" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {/if}
            {:else}
              <td month="current" events={eventsForDay(day).length} today={day.isToday()} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
            {/if}
          {/each}
        </tr>
      {/each}
    </tbody>
    <tfoot>
      <tr>
        <td colspan="7">
          <Row>
            <RowCell style="width: 15%">
              <button type="icon" on:click={togglepushMode}>                
                {#if pushMode == 1}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24" skip-icon><path d="M19 3H18V1H16V3H8V1H6V3H5C3.89 3 3 3.89 3 5V19C3 20.1 3.89 21 5 21H19C20.1 21 21 20.1 21 19V5C21 3.89 20.1 3 19 3M19 19H5V8H19V19M12 17V15H8V12H12V10L16 13.5L12 17Z"/></svg>
                {:else}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24" skip-icon><path d="M19 3H18V1H16V3H8V1H6V3H5C3.89 3 3 3.89 3 5V19C3 20.1 3.89 21 5 21H19C20.1 21 21 20.1 21 19V5C21 3.89 20.1 3 19 3M19 19H5V8H19V19M12 10V12H16V15H12V17L8 13.5L12 10Z"/></svg>
                {/if}
              </button>
            </RowCell>            
            <RowCell style="width: 17%">
              <button pushshortcut type="button" on:click={pushDay(1)}>D</button>
            </RowCell>            
            <RowCell style="width: 17%">
              <button pushshortcut type="button" on:click={pushWeek}>W</button>
            </RowCell>
            <RowCell flex="flex" style="width: 17%">
              <button pushshortcut type="button" on:click={pushMonth}>M</button>
              </RowCell>
            <RowCell style="width: 17%">
              <button pushshortcut flex="flex" type="button" on:click={pushQuarter}>Q</button>
            </RowCell>
            <RowCell flex="flex" style="width: 17%">
              <button pushshortcut type="button" on:click={pushYear}>Y</button>
            </RowCell>
          </Row>
        </td>
      </tr>
      <tr>
        <td colspan="7">
          <Row>
            <RowCell style="width: 15%">
              <button type="icon" on:click={togglepushMode}>
                {#if pushMode == 1}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24" skip-icon><path d="M19 19V8H5V19H19M16 1H18V3H19C20.11 3 21 3.9 21 5V19C21 20.11 20.11 21 19 21H5C3.89 21 3 20.1 3 19V5C3 3.89 3.89 3 5 3H6V1H8V3H16V1M11 9.5H13V12.5H16V14.5H13V17.5H11V14.5H8V12.5H11V9.5Z"/></svg>
                {:else}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24" skip-icon><path d="M19 19V8H5V19H19M16 1H18V3H19C20.11 3 21 3.9 21 5V19C21 20.11 20.11 21 19 21H5C3.89 21 3 20.1 3 19V5C3 3.89 3.89 3 5 3H6V1H8V3H16V1M16 12.5V14.5H8V12.5H16Z"/></svg>
                {/if}
              </button>
            </RowCell>            
            <RowCell style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(14)}>14</button>
            </RowCell>
            <RowCell flex="flex" style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(30)}>30</button>
              </RowCell>
            <RowCell style="width: 21%">
              <button pushshortcut flex="flex" type="button" on:click={pushDay(90)}>90</button>
            </RowCell>
            <RowCell flex="flex" style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(180)}>180</button>
            </RowCell>
          </Row>
        </td>        
      </tr>
    </tfoot>

  </table>
</div>

<style lang="sass">[container] {
  display: block; }

