  background: transparent;
  text-align: center;
  text-transform: uppercase;
  width: 100%; }
  button[dayshortcut]:hover, button[pushshortcut]:hover {
    color: var(--blue); }

button[pushshortcut] {
  padding: 1px 12px;
  font-size: 16px;
  font-weight: bold; }

[monthname] {
  font-weight: bold;
  text-align: center;
