<script lang="coffeescript">var changeIndex, checkTrigger, dispatch, hasfocus, highlightedIndex, menuId, menuItemClick, menuRef, overlayClick, style;

import {
  createEventDispatcher,
  onMount,
  afterUpdate
} from 'svelte';

import {
  slide
} from 'svelte/transition';

dispatch = createEventDispatcher();

export var disabled = false;

export var id = uuid();

export var items = [];

export var width = '200px';

export var selectedIndex = -1;

export var size = void 0;

export var value = "";

export var blankSlate = "";

export var labelKey = "label";

export var idKey = "id";

export var showMenu = false;

export var align = "left";

// trigger is required
export var trigger = void 0;

// if a trigger is defined, it maps the width to the parent
// and is placed just below it, used for combobox and custom select
export var matchTrigger = false;

style = void 0;

menuRef = void 0;

hasfocus = false;

highlightedIndex = -1;

changeIndex = function(direction) {
  var index;
  index = highlightedIndex + direction;
  if (index < 0) {
    index = items.length - 1;
  } else if (index >= items.length) {
    index = 0;
  }
  return highlightedIndex = index;
};

menuId = uuid();

onMount(function() {
  return console.log("parent, mounted");
});

checkTrigger = function(i) {
  var right, triggerContainerWidth, triggerCoords;
  if (trigger != null) {
    if (matchTrigger) {
      style = `width: ${width};top: ${trigger.clientHeight + 4}px; width: ${trigger.clientWidth + 2}px`;
    } else {
      if (align === "left") {
        style = `width: ${width};top: ${trigger.offsetParent.clientHeight + 4}px;`;
