<script lang="coffeescript">// Project Show
var addHandler, addListItem, cloneChildItem, cloneRootItem, deferredUpdate, destroyList, destroyListItem, dividerDropHandler, dropHandler, getList, isDragging, nestHandler, refreshAndUpdate, resource, setDragging, topDropHandler, unsetDragging, update, updateAttribute, updateChildItem, updateRootItem;

import {
  push,
  pop,
  replace,
  location
} from 'svelte-spa-router';

import RootItem from '../../helpers/root_item.js';

import {
  Row,
  RowCell
} from "../../components/Flex";

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte";

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import Progress from '../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../_Common/Popovers/DueDate.svelte';

import Menu from "../_Common/Menus/List.svelte";

import ListItems from './_ListItems.svelte';

import DroppableDivider from "../Lists/Sections/Nestable/NestableDivider.svelte";

// Local vars
export var params = {};

export var list = void 0;

// --------------------------------------------------------------------------------------------------------------------
resource = new RootItem;

isDragging = false; // global state

setDragging = function() {
  console.log("Show - dragging");
  return isDragging = true;
};

unsetDragging = function() {
  console.log("Show - stop dragging");
  return isDragging = false;
};

getList = function() { // get list from db and refresh local view
  var scb;
  scb = function(doc) {
    list = doc;
    return console.log(list);
  };
  return resource.get(params.id, scb, null, null);
};

refreshAndUpdate = function() {
  list = list; // svelte refresh
  unsetDragging();
  return deferredUpdate(getList);
};

update = function(scb, ecb, fcb) { // calling directly breks scope, so call within update fn
  return resource.update(scb, ecb, null);
};

deferredUpdate = update.debounce(500);

cloneRootItem = function(e) {
  var scb;
  console.error("cloneRootItem");
  // original = new RootItem(doc.clone)
  scb = function(d) {
    console.error("cloneRootItem scb");
    return console.debug(d);
  };
  return list.clone(scb);
};

// console.table clone

// CRUD EVENTS

// update root list attribute
updateAttribute = function(e) {
  Object.merge(list, e.detail);
  console.error("updateAttribute");
  console.debug(list);
  return refreshAndUpdate();
};

// Child Events
updateChildItem = function(e) { // update a child in the list_items hierarchy
  var change, originalItem, originalItemIndex, originalItemReference;
  console.error("updateChildItem");
  console.log(e.detail);
  change = e.detail;
  originalItemReference = list.reference_from_ancestry(change.ancestry);
  originalItemIndex = originalItemReference.list_items.findIndex({
    uuid: change.uuid
  });
  originalItem = originalItemReference.list_items[originalItemIndex];
  Object.merge(originalItem, change);
  return refreshAndUpdate();
};

addListItem = function(event) { // add to this item only, not to children
  return resource.add_list_item(event.detail, null, refreshAndUpdate);
};

destroyListItem = function(e) {
  return resource.delete_list_item(e.detail, refreshAndUpdate);
};

cloneChildItem = function(e, scb) {
  console.error("LISTITEM SHOW CLONE CHILD ITEM");
  console.log(e.detail);
  console.log("-----");
  return resource.cloneListItem(e.detail, refreshAndUpdate);
};

// List
destroyList = function(e) {
  var scb;
  console.error("DESOTRY LIST");
  scb = function() {
    return push("/lists");
  };
  return resource.destroy(list.id, scb);
};

updateRootItem = function(e) {
  var ecb;
  ecb = function(err) {};
  // refreshList()
  return resourceList.patch(e.detail, refreshList, ecb, null);
};


// Drag Drop
topDropHandler = function(event) {
  var destination, ecb, origin;
  console.error("DROP ON ROOT LIST");
  console.debug(event.detail);
  origin = event.detail.dragItem;
  destination = resource;
  // event.detail.dropItem.inderx
  // index = event.detail.dropItem.index
  destination["to_index"] = event.detail.dropItem.index;
  console.error("DESTINATION");
  console.log(destination);
  ecb = function(e) {
    console.log("drophandler error");
    return console.log(e);
  };
  return resource.drop_item(origin, destination, refreshAndUpdate, ecb);
};

dividerDropHandler = function(event) {
  return console.error("dividerDropHandler");
};

nestHandler = function(event) {
  var destination, ecb, origin;
  origin = event.detail.dragItem;
  destination = event.detail.dropItem;
  ecb = function(e) {
    console.log("nesthandler error");
    return console.log(e);
  };
  return resource.nest_item(origin, destination, refreshAndUpdate, ecb);
};

dropHandler = function(event) {
  var destination, ecb, origin;
  console.error("SHOW dropHandler");
  console.debug(event.detail);
  origin = event.detail.dragItem;
  destination = event.detail.dropItem;
  ecb = function(e) {
    console.log("drophandler error");
    return console.log(e);
  };
  return resource.drop_item(origin, destination, refreshAndUpdate, ecb);
};

addHandler = function(event) {
  // console.error "SHOW addHandler"
  // console.log event.detail
  return resource.insert_item({
    uuid: uuid(),
    name: `New item created at ${Date.create().format('{HH}:{mm}:{ss}')}`
  }, event.detail, refreshAndUpdate);
};

// Reactive
$: title = list ? list.name : "Unnamed list";

// handle a reroute
$: if (params.id) { getList() };
</script>

<svelte:head>
  <title>Lists :: {title}</title>
</svelte:head>
{#if list}

  <PageContent>

    
    <PageHeader icon={true}>
      <div slot="icon"><Progress bind:value={list.progress} item={list} on:update={updateAttribute} size={36}/></div>
      <Row style="max-width: 100%;">
        <RowCell flex style="overflow-x: hidden">
          <h1>
          <InlineInput bind:value={list.name} on:update={deferredUpdate} style="margin-left: -2px" inputStyle="margin-top: 2px; margin-left: -1px"/>
          </h1>
        </RowCell>
        <RowCell style="padding-top: 16px; width: 32px" flex="initial">
          <Menu bind:item={list} on:select={updateAttribute} on:clone={cloneRootItem} on:destroy={destroyList}/>
        </RowCell>
      </Row>
      <div slot="secondary-info">
        <div layout="row">

          <!-- due date menu -->
          <div flex="initial" present={list.due_date != null}>
            <DueDatePopover item={list} value={list.due_date} on:update={updateAttribute} on:open on:close isListItem={false} idAttribute="id"/>
          </div> 
        </div>
      </div>
    </PageHeader>

    
    <hr/>
    
    
    <DroppableDivider item={{ list_items: [],ancestry: [], index: -1} } on:drop={dropHandler} on:add={addHandler} bind:dragState={isDragging}/>
    <ListItems bind:list={list} bind:dragState={isDragging} on:clone={cloneChildItem} on:update={updateChildItem} on:drop={dropHandler} on:dividerDrop={dividerDropHandler} on:nest={nestHandler} on:add={addHandler} on:destroy={destroyListItem} on:dragging={setDragging} on:dragStop={unsetDragging}/>

    <DroppableDivider item={{ list_items: [], ancestry: [], index: list.list_items.length + 1 }} bind:dragState={isDragging} on:drop={dropHandler} on:add={addHandler}/>
    {#if list.is_archived == false}
    <QuickEntry on:add={addListItem} stubPlaceholder="Add Top Level List Item" />
    {/if}

  
  </PageContent>
  
{/if}
<style lang="sass">h6 {
  text-transform: uppercase;
  margin-bottom: -4px;
  color: var(--gray);
  letter-spacing: 0.75px; }

hr {
  margin-bottom: 36px; }

