    :global([type="radar-list"]:hover) :global([show-on-hover]) :global(svg) :global(path) {
      fill: var(--gray); }
    :global([type="radar-list"]:hover) :global([show-on-hover]:hover) :global(svg) :global(path) {
      fill: var(--blue); }
