<script lang="coffeescript">var checkTypesForListId, dispatch, draggable, draggableEl, drop, end, getWidth, indentButtonRef, isInvalid, isOver, leave, mousedown, mouseup, over, start, toggleExpanded;

import {
  Row,
  RowCell
} from "../../../../components/Flex";

import Menu from '../../../../components/Overlay/Menu/ComposedMenu.svelte';

import Indent from "../Indent.svelte";

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var index = void 0;

export var item = void 0;

export var indent = 0;

export var listId = void 0;

export var sameListOnly = false;

export var showMenu = true;

export var dragState = void 0;

indentButtonRef = void 0;

draggableEl = void 0;

draggable = false;

isOver = false;

isInvalid = false;

checkTypesForListId = function(types) {
  return types.find(function(t) {
    return t.startsWith("listid:");
  });
};

start = function(e) {
  item["listId"] = listId;
  item["index"] = index || null;
  e.dataTransfer.setData("text/plain", JSON.stringify(item));
  e.dataTransfer.setData(`listid:${listId}`, JSON.stringify(item));
  return dispatch("dragging", true);
};

end = function(e) { // stop dragging
  return dispatch("dragStop", true);
};

over = function(e) {
  var draggedListId;
  e.preventDefault();
  isOver = true;
  if (!!sameListOnly) {
    draggedListId = checkTypesForListId(e.dataTransfer.types);
    if (draggedListId != null) {
      return isInvalid = !(listId === draggedListId.replace("listid:", ""));
    } else {
      return isInvalid = true;
    }
  }
};

leave = function(e) {
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, dropped;
  isOver = false;
  isInvalid = false;
  draggable = false;
  e.preventDefault();
  dispatch("dragStop", true);
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  dropped = true;
  if (!!sameListOnly) {
    // console.log "This list: #{listId} :: itemList #{dragItem.listId}"
    dropped = listId === dragItem.listId;
  }
  if (dropped) {
    return dispatch("nest", {
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};

mouseup = function() {
  draggable = false;
  return isInvalid = false;
};

mousedown = function() {
  return draggable = true;
};

getWidth = function(el) {
  if (el != null) {
    return el.clientWidth - 48;
  } else {
    return 0;
  }
};

toggleExpanded = function() {
  console.log("toggle Expanded");
  item.is_expanded = !item.is_expanded;
  return dispatch("update", {
    uuid: item.uuid,
    is_expanded: item.is_expanded,
    ancestry: item.ancestry
  });
};

$: draggableWidth = getWidth(draggableEl) ;
</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  bind:this={draggableEl}
  layout="row"
>
      <div flex="initial" handle on:mouseup={mouseup(item)} on:mousedown={mousedown(item)} show-on-hover>
        <slot name="grabber">
          <svg class="handle" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z"/></svg>
        </slot>
      </div>

      <div flex class="slot">
          <div layout="row" item-interior>
          <div flex="initial" style="width: {indent * 24}px">
            <Indent level={indent}/>
          </div>
          
          <div flex="initial" collapsible>
          {#if item.list_items.length > 0}
            <button icon on:click={toggleExpanded}>
              {#if item.is_expanded}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M7 10l5 5 5-5H7z"/></svg>
              {:else}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M10 17l5-5-5-5v10z"/></svg>
              {/if}
            </button>
          {/if}
          </div>
          <div flex>
            <slot item={item} index={index}/>
          </div>
        </div>
      </div>
      <div flex="initial" menu draggable-item-menu show-on-hover>
        <slot name="menu"/>
      </div>

</div>
